<template>
  <div class="showtags">
      <h6>热门搜索</h6>
      <Tag  v-for="(item, index) in hots" :key="index" :hot="item" :index = "index" @changeval="$emit('changeval',$event)"></Tag>
    </div>
</template>

<script>

import Tag from "../components/Tag.vue"
export default {
  data() {
    return {
      hots:null,
      tindex:null,
     
    }
  },
  components: {
    Tag
  },
  methods: {
    getSearchHot(){
      this.axios.get('http://music.kele8.cn/search/hot').then(responsive => {
        window.console.log(responsive);
        ( { hots : this.hots } = responsive.data.result )
      })
    }
  },
  created() {
    this.getSearchHot()
  },
};
</script>

<style lang="less" scoped>
.showtags{
    box-sizing: border-box;
    padding: 15px;
    h6{
      font-size: 12px;
      line-height: 2.5;
    }
  }
</style>