<template>
  <li class="card" @click="handleClick(item.id)">
    <div class="thumb">
      <img :src="item.picUrl" alt />
     <div class="count">
        <i class="iconfont icon-earphone"></i>
      <span>{{ translateCount(item.playCount) }}</span>
     </div>
    </div>
    <div class="remd_text">{{ item.name }}</div>
  </li>
</template>

<script>
export default {
 
  props: {
    item: Object
  },
  methods: {
    translateCount(n) {
      if (n > 99999999) {
          return (n/100000000).toFixed(1) + '亿'
      }
      if (n > 9999) {
          return (n/10000).toFixed(1) + '万'
      }
    },
    handleClick(param){
       this.$router.push({ path: "playlist", query: { id: param } });
  }
  },
  
};
</script>

<style lang="less" scoped>
li {
  .thumb {
    position: relative;
    img {
      vertical-align: middle;
    }
    .count {
      position: absolute;
      right: 5px;
      top: 5px;
      font-size: 10px;
      color: #fff;
      i{
        font-size: 5px;
        margin-right: 5px;
      }
    }
  }
  .remd_text {
    display: -webkit-box;
    font-size: 14px;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin: 0 5px 10px 5px;
    
  }
}
</style>