<template>
  <div id="app">
    <keep-alive>
      <router-view @setSongId="currentobj=$event" exclude="playlist"/>
    </keep-alive>
    <Player :currentobj="currentobj" @next="ncheckSongIdIndex($event[0],$event[1],$event[2])" @prev="pcheckSongIdIndex($event[0],$event[1],$event[2])" @playListId = "currentobj = $event"></Player>
  </div>
</template>

<script>
import Player from "./components/Player.vue"
export default {
  data() {
    return {
      currentobj:null,
    }
  },
  components:{
    Player
  },
  methods: {
       ncheckSongIdIndex(id, arr,playcode) {
         let index = 1
         let len = arr.length
      for (var i = 0; i < len; i++) {
        if (id == arr[i].id) {
         switch (playcode){
           case 1 : {
             index = i + 1 > arr.length - 1 ? 0 : i + 1;break;
           }
           case 2 : {
             index = i ;break;
           }
           case 3 : {
            index = randomSongIndex();break;
           }
           case 4 : {
             index = i + 1 > arr.length - 1 ? 0 : i + 1;break;
           }
         }
          this.currentobj = arr[index];
        }
      }
      function randomSongIndex() { 
        return Math.floor(Math.random()*len)
       }
    },
    pcheckSongIdIndex(id, arr,playcode) {
      let index = 1
      let len = arr.length
      for (var i = 0; i < arr.length; i++) {
        if (id == arr[i].id) {
          switch (playcode){
           case 1 : {
            index = i - 1 < 0 ? arr.length - 1 : i - 1;break;
           }
           case 2 : {
             index = i ;break;
           }
           case 3 : {
            index = randomSongIndex();break;
           }
           case 4 : {
            index = i - 1 < 0 ? arr.length - 1 : i - 1;break;
           }
         }
          this.currentobj = arr[index];
        }
      }
      function randomSongIndex() { 
        return Math.floor(Math.random()*len)
       }
    }
  },
};
</script>

