<template>
  <form @submit.prevent="jumpview()" action="">
    <span @click="jumpview()">
      <i class="iconfont icon-aui-icon-search"></i>
    </span>
    <input ref="input" type="text" name id placeholder="搜索歌曲、歌手、专辑" v-model.trim="val" @focus="jumpsug" />
    <span class="sayno">
      <i v-if="val" class="iconfont icon-quxiao" @click="val=null"></i>
    </span>
  </form>
</template>

<script>
export default {
  props: ["hots", "tindex", "sug", "thot"],
  data() {
    return {
      val: null,
    };
  },
  watch: {
    val() {
      this.translateVal();
      if(!this.val){
        window.console.log('kong')
        this.$refs.input.blur();
         this.$router.push({ path: "/search/hotsearch" });
      }
    },
    sug() {
      this.val = this.sug;
      this.translateVal();
      this.jumpview();
    },
    thot() {
      this.val = this.thot;
      this.translateVal();
      this.jumpview();
    }
  },
  methods: {
    translateVal() {
      this.$emit("receiveVal", this.val);
      window.console.log(1);
      
    },
    jumpview() {
      window.console.log(this.$refs.input)
      this.$refs.input.blur();
      this.$router.push({ path: "/search/searchresult" });
    },
    jumpsug() {
      
      this.$router.push({ path: "/search/searchsuggests" });
    }
  }
};
</script>

<style lang="less" scoped>
form {
  padding: 15px 10px;
  display: flex;
  border-bottom: 1px solid #e6e6e6;
  span {
    line-height: 30px;
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 15px 0 0 15px;
    background: rgb(235, 236, 236);
    text-align: center;
    &.sayno {
      border-radius: 0 15px 15px 0;
    }
    i.iconfont {
      color: #bcbdbd;
    }
  }
  input {
    flex: 1;
    border: none;
    outline: none;
    height: 30px;
    background: rgb(235, 236, 236);
    &::placeholder {
      color: #bcbdbd;
    }
  }
}
</style>