<template>
  <div class="home-recommend">
    <HomeTitle>推荐歌单</HomeTitle>
    <ul class="list">
      <SongListCard
        v-for="(item, index) in personalized"
        :key="index"
        :item="item"
        :is-show-quality="false"
      ></SongListCard>
    </ul>
    <HomeTitle>最新音乐</HomeTitle>
    <ol>
      <SongListItem
        v-for="(item, index) in newsongs"
        :key="index"
        :playlist="item"
        :songarr = "newsongs"
        :is-show-quality="true"
        @setSongId="translateCurrentId($event)"
      ></SongListItem>
    </ol>
  </div>
</template>

<script>
import HomeTitle from "../components/HomeTitle.vue";
import SongListCard from "../components/SongListCard.vue";
import SongListItem from "../components/SongListItem.vue";
export default {
  data() {
    return {
      personalized: null,
      newsongs: null
    };
  },
  components: {
    HomeTitle,
    SongListCard,
    SongListItem
  },
  methods: {
    getPersonalized() {
      let personalized = JSON.parse(
        window.localStorage.getItem("personalized")
      );
      if (personalized && personalized.time > Date.now()) {
        this.personalized = personalized.data;
      } else {
        this.axios
          .get("http://music.kele8.cn/personalized?limit=6")
          .then(responsive => {
            // window.console.log(responsive);
            ({ result: this.personalized } = responsive.data);
            window.localStorage.setItem(
              "personalized",
              JSON.stringify({
                time: Date.now() + 30 * 60 * 1000,
                data: this.personalized
              })
            );
          });
      }
    },
    getNewSong() {
      let newsongs = JSON.parse(window.localStorage.getItem("newsongs"));
      if (newsongs && newsongs.time > Date.now()) {
        this.newsongs = newsongs.data;
      } else {
        this.axios
          .get("http://music.kele8.cn/personalized/newsong")
          .then(responsive => {
            // window.console.log(responsive);
            ({ result: this.newsongs } = responsive.data);
            // window.console.log(this.newsongs);
            window.localStorage.setItem(
              "newsongs",
              JSON.stringify({
                time: Date.now() + 30 * 60 * 1000,
                data: this.newsongs
              })
            );
          });
      }
    },
    translateCurrentId(id) {
      this.$emit("setSongId", id);
    }
  },
  created() {
    this.getPersonalized();
    this.getNewSong();
  }
};
</script>

<style lang="less">
.home-recommend {
  .list {
    display: flex;
    flex-wrap: wrap;
    li.card {
      flex: 1 0 30%;
      &:nth-child(3n + 2) {
        margin: 0 3px;
      }
    }
  }
}
</style>