<template>
  <div class="home-hot">
    <div class="header">
      <div class="hotopct">
         <div class="titles"></div>
         <div class="updatatime">
           更新日期：{{ timestampToTime(time) }}
         </div>
      </div>
    </div>
    <template v-if="isLoading">
    
        <van-loading color="#1989fa"/>
     
    </template>
    <template v-else>
      <ul>
      <SongListItem
        v-for="(item, index) in hotsongs"
        :key="index"
        :playlist="item"
        :songarr = "hotsongs"
        :is-show-quality="true"
        :index="index"
        @setSongId="$emit('setSongId',$event)"
      ></SongListItem>
    </ul>
    </template>
  </div>
</template>

<script>
import SongListItem from "../components/SongListItem.vue";
export default {
  data() {
    return {
      hotsongs:null,
      time:null,
      isLoading : true
    };
  },
  components: {
    SongListItem
  },
  methods: {
     getHotSong(){
      this.axios
        .get("http://music.kele8.cn/top/list?idx=1")
        .then(responsive => {
          window.console.log(responsive); 
           ({ tracks: this.hotsongs } = responsive.data.playlist);
           ({ updateTime: this.time } = responsive.data.playlist);
            this.isLoading = false
          // window.console.log(this.newsongs); 

        });
    },
    timestampToTime(timestamp){
      var date = new Date(timestamp )
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '月';
        var D = date.getDate() + ' 日';
        return M+D

    }
  },
  created() {
    this.getHotSong()
  },
};
</script>

<style lang="less" scoped>
  .header{
    height: 140px;
    background: url('../assets/hot_music_bg_2x.jpg') no-repeat;
    background-size: cover;
    background-position: center;
     display: flex;
     position: relative;
     z-index: 1;
     &::after{
       content: '';
       display: block;
       position: absolute;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       background:rgba(0, 0, 0, 0.2)
     }
    .hotopct{
      padding-left: 15px;
      margin:auto 0;
       position: relative;
      z-index: 2;
      .titles{
     
      display: inline-block;
      width: 142px;
      height: 67px;
      background: url('../assets/index_icon_2x.png') no-repeat;
      background-position: -24px -30px;
      background-size: 166px 97px;
    }
    .updatatime{
      color: rgba(255, 255, 255, 0.8);
      font-size: 12px; 
      margin-top: 5px;
    }
    }
    
  }
  .van-loading{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%) 
  }
</style>