<template>
  <li class="song" :class="{isdefalutbg : isShowQuality} " @click="translateCurrentId(playlist)">
    <div class="left" v-if="index !== undefined">
      <span>{{ index+1 }}</span>
    </div>
    <dl class="center">
      <dt v-html="hightL(playlist.name)"> <span v-if="playlist.song && playlist.song.alias[0]">{{ playlist.song.alias[0] }}</span></dt>
      <dd v-if="playlist.song">
        <span class="hquality" v-if="isShowQuality">SQ</span>
        {{ playlist.song.artists[0].name }} - {{ playlist.song.album.name }}
      </dd>
      <dd v-else-if="playlist.ar ">
        <span class="hquality" v-if="isShowQuality">SQ</span>
        {{ playlist.ar[0].name }} - {{ playlist.al.name }}
      </dd>
    <dd v-else>
        <span class="hquality" v-if="isShowQuality">SQ</span>
        {{ playlist.artists[0].name }} - {{ playlist.album.name }}
      </dd>
    </dl>
    <div class="right">
      <i class="iconfont icon-play"></i>
    </div>
  </li>
</template>

<script>
export default {
    props:['playlist','index' ,'isShowQuality','str',"songarr"],
    methods: {
      translateCurrentId(id){
        window.console.log(id)
        this.$emit('setSongId',id)
        this.storageSongarr()
      },
      hightL(str){
        return str.replace(this.str,'<span class="hl">'+ this.str +'</span>')
    },
    storageSongarr(){
      window.localStorage.setItem('songArr',JSON.stringify(this.songarr))
    }
    },
};
</script>

<style scoped lang='less'>
li{
  display: flex;
  padding: 5px 0;
  border-bottom: 1px solid #e6e6e6;
  background: #F8F8F8;
  &.isdefalutbg{
    background: #FCFCFD;
  }
  .center{
    flex: 1;
    margin-left: 10px;
    dt{
      line-height: 20px;
      display: -webkit-box;
      -webkit-line-clamp:1;
      -webkit-box-orient: vertical;
      overflow: hidden;
       span {
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        color: #888;
      }
    }
    
    dd{
      line-height: 20px;
      font-size: 12px;
      display: -webkit-box;
      -webkit-line-clamp:1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      color: #888;
      span.hquality{
        // width: 12px;
        // height: 8px;
        // display: inline-block;
        // background: url('../assets/index_icon_2x.png') no-repeat;
        // background-position: 0 5px;
        font-size: 9px;
        color: #FE8A5E;
      }
    }
  }
  .left,
  .right{
    height: 40px;
    width: 32px;
    text-align: center;
    line-height: 40px;
     color: #999;
  .icon-play{
    display: block;
    font-size: 20px;
    
  }
}
}
</style>