<template>
  <div class="player" :class="{showplayer : isshowplayer}">
    <audio v-if="currentobj" autoplay :src="currentSongUrl" ref="audio"></audio>
    <div
      class="playface"
      :class="{ active: currentobj ,changebg : isshowplayer} "
      @click="isshowplayer = true"
    >
      <div class="slider" v-if="isshowplayer">
        <span>{{ currentTime }}</span>
        <label for>
          <div class="slidebg">
            <div class="slide" ref="slide"></div>
          </div>
          <input
            type="range"
            name
            id
            v-model="songCurrentTime"
            @input="changeCurrentTime"
            :max="songmsgs[0].dt/1000"
            v-if="isshowplayer"
          />
        </label>
        <span>{{ songTime }}</span>
      </div>
      <div class="facecontent">
        <div class="author-pic" v-if=" !isshowplayer ">
          <div class="warp-apic" ref="warpapic">
            <img v-if="songmsgs" :src="songmsgs[0].al.picUrl" alt />
          </div>
          <div v-if="songmsgs" class="song-msg">
            <h6>{{ songmsgs[0].name }}</h6>
            <h6>{{ songmsgs[0].ar[0].name }}</h6>
          </div>
        </div>
        <div class="ctrl">
          <template v-if="!isshowplayer">
            <span class="ctrlitem">
              <i class="iconfont icon-PrevioustrackFill" @click.stop="prev"></i>
            </span>
            <span class="ctrlitem">
              <i v-if="isplay" class="iconfont icon-el-hg-icon-stop" @click.stop="pause"></i>
              <i v-else class="iconfont icon-play1" @click.stop="play"></i>
            </span>
            <span class="ctrlitem">
              <i class="iconfont icon-NexttrackFilled" @click.stop="next"></i>
            </span>
          </template>
          <template v-else>
            <span class="ctrlitem">
              <i
                v-if="playcode == 1"
                class="iconfont icon-loop changewhite"
                @click.stop="playcode=2"
              ></i>
              <i
                v-if="playcode == 2"
                class="iconfont icon-24gl-repeatOnce2 changewhite"
                @click.stop="playcode=3"
              ></i>
              <i
                v-if="playcode == 3"
                class="iconfont icon-random1 changewhite"
                @click.stop="playcode=4"
              ></i>
              <i
                v-if="playcode == 4"
                class="iconfont icon-order-play-fill changewhite"
                @click.stop="playcode=1"
              ></i>
            </span>
            <span class="ctrlitem">
              <i class="iconfont icon-Previoustrack changewhite" @click.stop="prev"></i>
            </span>
            <span class="ctrlitem">
              <i v-if="isplay" class="iconfont icon-stop1 changewhite" @click.stop="pause"></i>
              <i v-else class="iconfont icon-play2 changewhite" @click.stop="play"></i>
            </span>
            <span class="ctrlitem">
              <i class="iconfont icon-Nexttrack changewhite" @click.stop="next"></i>
            </span>
            <span class="ctrlitem">
              <i class="iconfont icon-order-list changewhite" @click.stop="rise"></i>
            </span>
          </template>
        </div>
      </div>
    </div>
    <div class="playshow">
      <div class="title-top" v-if="isshowplayer">
        <i class="iconfont icon-previous goback" @click.stop="isshowplayer = false"></i>
        <div class="song-au">
          <h5>{{ songmsgs[0].name }}</h5>
          <h6>{{ songmsgs[0].ar[0].name }}</h6>
        </div>
      </div>
      <div
        class="mask"
        v-if="songmsgs"
        :style="{'backgroundImage': `url(${songmsgs[0].al.picUrl}) `}"
      ></div>
      <div class="maskz"></div>
      <div
        class="songpic"
        v-if="songmsgs && !isShowLyric"
        ref="songpic"
        :style="{'backgroundImage': `url(${songmsgs[0].al.picUrl})`}"
        @click="isShowLyric = !isShowLyric"
      ></div>
      <div class="lyric" v-if="isShowLyric" @click="isShowLyric = !isShowLyric" >
        <div class="line" v-if="sgin"></div>
        <ul class="lyriclist" ref="lyriclist" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd">
          <li ref="li" v-if="!lyric">{{ lyricArr }}</li>
          <template v-else-if="Array.isArray(lyricArr[0])">
            <li ref="li" v-for="(item, index) in lyricArr" :key="index">{{ item[1] }}</li>
          </template>
          <template v-else>
            <li ref="li" v-for="(item, index) in lyricArr" :key="index">{{ item }}</li>
          </template>
        </ul>
      </div>
    </div>
    <div class="playlist" :class="{ rise : isrise }">
      <ul class="list">
        <p>歌曲( {{ songsarrlen }} )</p>
        <li
          class="item"
          v-for="(item, index) in songsarr"
          :key="index"
         
        >
          <div class="songname"  @click="$emit('playListId',item),isrise = false">
            <h6>
              {{ item.name }}
              <span v-if="item.ar">-{{ item.ar[0].name}}</span>
              <span v-else>-{{ item.song.artists[0].name }}</span>
            </h6>
          </div>
          <span>
            <i class="iconfont icon-quxiao1" @click="removeLi(index)"></i>
          </span>
        </li>
      </ul>
      <div class="maskp" :class="{ risemask : isrise }" @click="isrise = false"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["currentobj"],
  data() {
    return {
      isplay: true,
      songmsgs: null,
      songCurrentTime: 0,
      songTime: "00:00",
      timer: null,
      isshowplayer: false,
      isrise: false,
      songsarr: [],
      songsarrlen: 0,
      playcode: 1,
      checkPlayError: 1,
      lyric: "",
      lyricArr: null,
      isShowLyric: false,
      oldY:0,
      newY:0,
      saveY:0,
      falg: false,
      resultY:0,
      sgin:false,
      canplay:false
    };
  },
  computed: {
    currentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.currentobj.id +
        ".mp3"
      );
    },
    currentTime() {
      let min =
        parseInt(this.songCurrentTime / 60) > 9
          ? parseInt(this.songCurrentTime / 60)
          : "0" + parseInt(this.songCurrentTime / 60) + ":";
      let scend =
        parseInt(this.songCurrentTime % 60) > 9
          ? parseInt(this.songCurrentTime % 60)
          : "0" + parseInt(this.songCurrentTime % 60);
      return min + scend;
    }
  },
  methods: {
    getSongDetail() {
      this.axios
        .get("http://music.kele8.cn/song/detail?ids=" + this.currentobj.id)
        .then(responsive => {
          ({ songs: this.songmsgs } = responsive.data);
        });
    },
    getLyric() {
      this.axios
        .get("http://music.kele8.cn/lyric?id=" + this.currentobj.id)
        .then(responsive => {
          if (responsive.data.nolyric) {
            this.lyricArr = "该歌曲没有歌词";
            this.lyric = false;
          } else {
            // window.console.log(Boolean(responsive.data.lrc && responsive.data.lrc.lyric))
            if (responsive.data.lrc && responsive.data.lrc.lyric) {
              ({ lyric: this.lyricArr } = responsive.data.lrc);
              this.getLyricArr(this.lyricArr);
              this.lyric = true;
              // window.console.log(responsive.data.lrc.lyric)
            } else {
              this.lyricArr = "该歌曲没有歌词";
              this.lyric = false;
              window.console.log('没有歌词啊')
            }
            
          }

          // window.console.log(this.lyric)
        });
    },
    getLyricArr(str) {
      let arr = str.split("\n");
      let newArr = [];
     if(arr[0].indexOf('[') != -1){
        for (var i = 0; i < arr.length; i++) {
        newArr.push(arr[i].split("]"));
        newArr[i][0] = newArr[i][0].slice(newArr[i][0].indexOf("[") + 1);
        newArr[i][0] =
          newArr[i][0].slice(0, newArr[i][0].indexOf(":")) * 60 +
          parseFloat(newArr[i][0].slice(newArr[i][0].indexOf(":") + 1));
       
       
      }
      //剔除时间为NaN的
     newArr = newArr.filter(function (elemt) { 
        return elemt[0].toString() != 'NaN'
       })
        this.lyricArr = newArr;
     }else{
       this.lyricArr = arr;
     }

     
      // window.console.log(resultArr);
    },
    getCurrentLyricIndex(nowTime, arr) {
      for (var i = arr.length - 1; i >= 0; i--) {
        if (arr[i][0] <= nowTime) {
          return i;
        }
      }
      return -1;
    },
    //歌词拖动
    touchStart(event){
      window.console.log("按下")
      window.clearInterval(this.timer)
      this.$refs.lyriclist.style.transition = "none"
      this.falg = true;
      let num = 0;
      window.console.log(event.touches[num]);
      this.oldY = event.touches[num].clientY
      if(this.$refs.lyriclist.style.marginTop){
          this.saveY = parseInt(this.$refs.lyriclist.style.marginTop)
        }
    },
    touchMove(event){
     window.console.log('拖动')
     this.sgin = true
     if(this.falg){
        
       let num = 0;
        this.newY = event.touches[num].clientY - this.oldY ;
        // window.console.log( this.$refs.lyriclist)
        this.resultY = this.newY + this.saveY
        this.$refs.lyriclist.style.marginTop = this.resultY + 'px'
       
     }
    },
    touchEnd(){
      window.console.log('抬起')
      this.saveCurrentTime()
      this.$refs.lyriclist.style.transition = "all .3s"
       if(this.sgin){
         let index = parseInt(Math.abs((this.resultY - 12.5)/25))
      this.$refs.audio.currentTime = this.lyricArr[index][0]
       }
      // window.console.log(index)
      this.sgin = false
      this.falg = false;
    },
    pause() {
      window.clearInterval(this.timer);
      if (!this.isshowplayer) {
        this.$refs.warpapic.style.animationPlayState = "paused";
      }
      if (this.$refs.songpic) {
        this.$refs.songpic.style.animationPlayState = "paused";
      }
      this.isplay = false;
      this.$refs.audio.pause();
    },
    play() {
      window.console.log("播放")
      window.clearInterval(this.timer);
      this.saveCurrentTime();
      if (!this.isshowplayer) {
        this.$refs.warpapic.style.animationPlayState = "running";
      }
      if (this.$refs.songpic) {
        this.$refs.songpic.style.animationPlayState = "running";
      }
      this.isplay = true;
      // this.timeout = setTimeout(()=>{
      //  this.next()
      //   window.alert('播放错误，已自动切换下一首')
      // },5000)
      if(this.$refs.audio.paused && this.canplay){
        this.$refs.audio.play();
      }
      this.$refs.audio.oncanplay = () => {
        this.$refs.audio.play();
        this.canplay = true
      }
      
      

    },
    next() {
      this.canplay = false
      window.clearInterval(this.timer);
      let songarr = JSON.parse(window.localStorage.getItem("songArr"));
      this.$emit("next", [this.currentobj.id, songarr, this.playcode]);
      if (this.playcode == 2) {
        this.$refs.audio.load();
      }
      this.play();
    },
    prev() {
      this.canplay = false
      window.clearInterval(this.timer);
      let songarr = JSON.parse(window.localStorage.getItem("songArr"));
      this.$emit("prev", [this.currentobj.id, songarr, this.playcode]);
      if (this.playcode == 2) {
        this.$refs.audio.load();
      }
      this.play();
    },
    saveCurrentTime() {
      this.timer = window.setInterval(() => {
        this.songCurrentTime = this.$refs.audio.currentTime;

        // window.console.log(this.$refs.audio.readyState)
        // if (
        //   this.$refs.audio.readyState == 4 &&
        //   this.$refs.audio.networkState == 2
        // ) {
        //   window.console.log('no')
        //   this.$refs.audio.load();
        //   this.play();
        //   return;
        // }
        // if (this.$refs.audio.readyState != 4) {
        //   this.next();
        // }
      }, 1000);
    },
    changeCurrentTime() {
      this.$refs.audio.currentTime = this.songCurrentTime;
    },
    computedSongTime() {
      let min =
        parseInt(this.songmsgs[0].dt / 1000 / 60) > 9
          ? parseInt(this.songmsgs[0].dt / 1000 / 60)
          : "0" + parseInt(this.songmsgs[0].dt / 1000 / 60) + ":";
      let scend =
        parseInt((this.songmsgs[0].dt / 1000) % 60) > 9
          ? parseInt((this.songmsgs[0].dt / 1000) % 60)
          : "0" + parseInt((this.songmsgs[0].dt / 1000) % 60);
      this.songTime = min + scend;
    },
    rise() {
      this.isrise = true;
      this.songsarr = JSON.parse(window.localStorage.getItem("songArr"));
      this.songsarrlen = this.songsarr.length;
    },
    removeLi(index){
      window.console.log(index)
      this.songsarr.splice(index,1)
      this.songsarrlen = this.songsarr.length;
      window.localStorage.setItem('songArr',JSON.stringify(this.songsarr))
    }
  },
  watch: {
    currentobj() {
      window.clearInterval(this.timer);
      this.getSongDetail();
      this.getLyric();
      this.saveCurrentTime();
    },
    songCurrentTime() {
      if (this.$refs.lyriclist) {
        var i = this.getCurrentLyricIndex(
          this.$refs.audio.currentTime + 1,
          this.lyricArr
        );
        //如果没找到就在最顶端
        i = i == -1 ? 0 : i;
        this.$refs.lyriclist.style.marginTop = -(i * 25 + 12.5) + "px";
        //当有active这个类名时 清除
        if (this.$refs.lyriclist.querySelector(".active")) {
          
          this.$refs.lyriclist.querySelector(".active").className = "";
        }
        //判断有没有歌词
        if (this.lyric == false) {
          
          //没有歌词就只能给一个li添加类名
          this.$refs.li.className = "active";
          
        } else {
          // window.console.log('xxx')
          this.$refs.li[i].className = "active";
        }
      }
      //进度条
      if (this.$refs.slide) {
        this.$refs.slide.style.width =
          (this.songCurrentTime / (this.songmsgs[0].dt / 1000)) * 100 + "%";
      }
      if (this.songCurrentTime >= this.songmsgs[0].dt / 1000) {
        this.next();
      }
    },
    songmsgs() {
      this.computedSongTime();
      this.play();
    }
  }
};
</script>

<style lang='less'>
.player {
  background: white;
  position: fixed;
  top: -100vh;
  transition: all 0.3s;
  &.showplayer {
    top: 0;
    z-index: 6;
  }
  .title-top {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 1;
    display: flex;
    align-items: center;
    color: white;
    .goback {
      margin-right: 15px;
    }
  }
  audio {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 60px;
    width: 100%;
  }
  .playface {
    width: 100%;
    height: 50px;
    position: fixed;
    bottom: -10vh;
    left: 0;
    background: #fcfcfd;
    border: 1px solid #e6e6e6;
    transition: all 0.3s;
    z-index: 3;
    &.active {
      bottom: 0vh;
    }
    &.changebg {
      height: 15vh;
      background: none;
      border: none;
      div.ctrl {
        align-items: inherit;
      }
    }
    .slider {
      display: flex;
      color: white;
      label {
        flex: 1;
        position: relative;
        input {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          opacity: 0;
        }
        .slidebg {
          width: 100%;
          height: 5px;
          background: gray;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);

          .slide {
            transition: all 0.3s;
            width: 0;
            height: 100%;
            background: limegreen;
          }
        }
      }
      span {
        font-size: 12px;
        width: 40px;
        text-align: center;
        line-height: 21px;
      }
    }
    .facecontent {
      height: 100%;
      padding: 5px;
      display: flex;
      .author-pic {
        width: 45%;
        height: 100%;
        display: flex;
        align-items: center;
        .warp-apic {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          border: 1px solid #e6e6e6;
          overflow: hidden;
          animation: myanimation 5s linear infinite;
          img {
            vertical-align: middle;
            width: 100%;
          }
        }
        .song-msg {
          flex: 1;

          h6 {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            font-size: 12px;
            padding-left: 5px;
          }
        }
      }
      .ctrl {
        flex: 1;
        display: flex;
        align-items: center;
        .ctrlitem {
          flex: 1;
          text-align: center;
          i.iconfont {
            line-height: 1.5;
            font-size: 2em;
            color: #d43c33;
            &.changewhite {
              color: white;
            }
          }
        }
      }
    }
  }
  .playshow {
    width: 100vw;
    height: 100vh;
    position: relative;

    .mask {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-position: center;
      background-size: cover;
      filter: blur(15px);
    }
    .maskz {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.3);
    }
    .songpic {
      width: 200px;
      height: 200px;
      border-radius: 50%;
      position: absolute;
      top: 40%;
      left: 50%;
      background-position: center;
      background-size: cover;
      border: 20px solid black;
      transform-origin: 50% 50%;
      animation: mybiganimation 8s linear infinite;
    }
    .lyric {
      position: absolute;
      bottom: 15vh;
      width: 100vw;
      height: 70vh;
      overflow: hidden;
      div.line {
        position: absolute;
        width: 90%;
        height: 1px;
        background: #fcfcfd;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        opacity: 0.5;
      }
      .lyriclist {
        transition: all 0.5s;
        height: 100%;
        position: absolute;
        width: 100%;
        top: 50%;
        li {
          min-height: 25px;
          font-size: 13px;
          line-height: 25px;
          text-align: center;
          color: #999;
          &.active {
            font-size: 16px;
            color: white;
          }
        }
      }
    }
  }
  .playlist {
    width: 100vw;
    position: fixed;
    height: 50vh;
    overflow-y: auto;
    bottom: -50vh;
    transition: all 0.3s;
    background: white;
    z-index: 999;
    border-radius: 15px 15px 0 0;
    &.rise {
      bottom: 0;
    }
    .list {
      padding: 15px;
      position: relative;
      z-index: 9;
      background: #fcfcfd;
      p {
        line-height: 40px;
        position: sticky;
        top: 0;
        background: #fcfcfd;
        border-bottom: 1px solid #e6e6e6;
      }
      .item {
        line-height: 40px;
        display: flex;

        .songname {
          width: 70%;
          display: flex;
          h6 {
            white-space: pre-line;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            display: -webkit-box;
            span {
              font-size: 13px;
              color: #999;
              text-align: left;
            }
          }
        }
        span {
          flex: 1;
          line-height: 40px;
          text-align: right;
        }
      }
    }
    .maskp {
      position: fixed;
      bottom: -100vh;
      width: 100vw;
      height: 100vh;
      z-index: 8;
      background: rgba(0, 0, 0, 0.3);
      &.risemask {
        bottom: 0;
      }
    }
  }
  @keyframes myanimation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes mybiganimation {
    from {
      transform: translateX(-50%) translateY(-50%) rotate(0deg);
    }
    to {
      transform: translateX(-50%) translateY(-50%) rotate(360deg);
    }
  }
}
</style>