import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)
import HomeRecommend from '../views/HomeRecommend.vue'
import HomeHot from '../views/HomeHot.vue'
import HomeSearch from '../views/HomeSearch.vue'
import HotSearch from '../views/HotSearch.vue'
import SearchResult from '../views/SearchResult.vue'
import SearchSuggests from '../views/SearchSuggests.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    redirect: '/recommend',
    children:[
      {
        path:'/recommend',
        component:HomeRecommend
      },
      {
        path:'/hot',
        component:HomeHot
      },
      {
        path:'/search',
        component:HomeSearch,
        redirect: 'search/hotsearch',
        children:[
          {
            path:'hotsearch',
            component:HotSearch
          },
          {
            path:'searchresult',
            component:SearchResult
          },
          {
            path:'searchsuggests',
            component:SearchSuggests
          }
        ]
      },
    ]
  },
  {
    path: '/playlist',
    name: 'playlist',
    component: () => import(/* webpackChunkName: "about" */ '../views/PlayList.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
