<template>
  <ul class="suggests">
    <h6 @click="$emit('sendsuggest',thinktosearch)">搜索"{{ thinktosearch }}"</h6>
    <li class="suggests-item" v-for="(item, index) in suggestsong" :key="index" @click="$emit('sendsuggest',item.keyword)">
      <i class="iconfont icon-aui-icon-search"></i>
      <h6>{{ item.keyword }}</h6>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["songname"],
  data() {
    return {
        suggestsong:null
    };
  },
  computed: {
    thinktosearch() {
      return  this.songname ;
    }
  },
  created() {
    this.getSuggest()
  },
  methods: {
    getSuggest() {
      if(this.songname){
          this.axios
        .get(
          "http://music.kele8.cn/search/suggest?keywords=" +
            this.songname +
            "&type=mobile"
        )
        .then(responsive => {
          window.console.log(responsive);
          ({ allMatch:this.suggestsong } = responsive.data.result)
        });
      }
    }
  },
  watch: {
    songname() {
      this.getSuggest();
    }
  }
};
</script>

<style lang="less">
.suggests {
  h6 {
    color: #507daf;
    line-height: 50px;
    margin-left: 10px;
    font-size: 15px;
    border-bottom: 1px solid #e6e6e6;
    }
    .suggests-item{
        display: flex;
        line-height: 50px;
        padding-left: 10px;
        i.iconfont{
            display: inline-block;
            line-height: 50px;
            font-size: 18px;
        }
        h6{
            flex: 1;
            color: #333;
            font-size: 15px;
        }
    }
  
}
</style>