<template>
  <div class="result">
    <h6>最佳匹配</h6>
    <template v-if="songmsg && songmsg.artist">
      <div class="artist-msg">
      <div class="msg-pic">
        <img :src="songmsg.artist[0].picUrl" alt="">
      </div>
      <p>歌手：{{ songmsg.artist[0].name }}</p>
      <i class="iconfont icon-next1"></i>
    </div>
    <div class="mv-msg" v-if="songmsg.mv">
      <div class="mv-pic">
        <img :src="songmsg.mv[0].cover" alt="">
      </div>
      <div class="in-msg">
        <h5>MV:{{ songmsg.mv[0].name }}</h5>
        <span>{{ songmsg.mv[0].artistName }}</span>
      </div>
       <i class="iconfont icon-next1"></i>
    </div>
    </template>
    <songListItem
      v-for="(item, index) in songresult.songs"
      :key="index"
      :is-show-quality="true"
      :playlist="item"
      :songarr = "songresult.songs"
      :str = "songval"
       @setSongId="$emit('setSongId',$event)"
    ></songListItem>
  </div>
</template>

<script>
import SongListItem from "../components/SongListItem.vue";
export default {
    props:['songname'],
  data() {
    return {
      songresult: [],
      songval : this.songname,
      songmsg : null,
    };
  },
  components: {
    SongListItem
  },
  created() {
         this.gteResultSong()
         this. getSongmsg()
  },
  methods: {
    gteResultSong() {
      if(this.songval){
        this.axios
        .get("http://music.kele8.cn/search?keywords=" + this.songval)
        .then(responsive => {
          ({ result: this.songresult } = responsive.data);
          // window.console.log(responsive)
        });
      }
    },
    getSongmsg(){
      if(this.songval){
        this.axios.get("http://musicapi.leanapp.cn/search/multimatch?keywords=" + this.songval)
        .then(responsive => {
            ({ result: this.songmsg } = responsive.data)
        })
      }
    }
  }
};
</script>

<style lang='less'>
.result{
 h6{
        font-size: 12px;
        line-height: 2;
        padding-left: 10px;
    }
   .hl{
        color: #507DAF
    }
    .artist-msg{
      display: flex;
      padding: 8px 15px 8px 0;
      margin-left: 10px;
      border-bottom: 1px solid #e6e6e6;
      line-height: 50px;
      .msg-pic{
        width: 50px;
        height:50px;
        margin-right: 15px;
        img{
          width: 100%;
          vertical-align: middle;
          display: inline-block;
        }
      }
      p{
        flex: 1;
        line-height: 50px;
      }
      i{
        line-height: 50px;
      }
    }
    .mv-msg{
      display: flex;
      padding: 8px 15px 8px 0;
      margin-left: 10px;
      border-bottom: 1px solid #e6e6e6;
      .mv-pic{
        width: 90px;
        margin-right: 10px;
        img{
          width: 100%;
          vertical-align: middle;
          display: inline-block;
        }
      }
      .in-msg{
        flex: 1;
        margin: auto 0;
        h5{
          color: #507DAF;
        }
        span{
          font-size: 12px;
          color: #999;
        }
      }
      i{
        line-height: 50px;
      }
    }
}
   
</style>