      @setSongId="$emit('setSongId',$event)"
      @sendsuggest="suggest=$event"
      @changeval="thot=$event"
    ></router-view>
  </div>
</template>

<script>
import SearchInput from "../components/SearchInput.vue";
export default {
  data() {
    return {
      receive: "",
      suggest: "",
      thot:''
    };
  },
  components: {
    SearchInput
  }
};
</script>

<style lang="less">
.home-search{
  height: 100vh;
}
